* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    position: relative;
}

.demo-section {
    position: relative;
    margin: 0;
    padding: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.icon-animation-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #06181f;
}

.icon-animation-body h2 {
    font-size: 32px;
    font-family: "arial";
    color: #31e8ff;
    margin-bottom: 40px;
}

.icon-animation-body p {
    font-size: 16px;
    font-family: "arial";
    color: #31e8ff;
    margin-top: 40px;
}

.icon-animation-body .icon-container {
    position: relative;
    width: 1000px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.icon-animation-body .icon-container .icon {
    width: 200px;
}

.icon-animation-body .icon-container .icon path {
    fill: transparent;
    stroke-width: 10;
    stroke: #31e8ff;
}

.icon-animation-body .icon-container .icon.heart path {
    stroke-dasharray: 1560;
    stroke-dashoffset: 0;
}

.icon-animation-body .icon-container .icon.heart:hover path {
    -webkit-animation: animate-heart 1.5s linear forwards;
            animation: animate-heart 1.5s linear forwards;
}

@-webkit-keyframes animate-heart {
    0% {
        stroke-dashoffset: 0;
    }
    40% {
        stroke-dashoffset: 1560;
    }
    80% {
        stroke-dashoffset: 3120;
        fill: transparent;
    }
    100% {
        stroke-dashoffset: 3120;
        fill: #31e8ff;
    }
}

@keyframes animate-heart {
    0% {
        stroke-dashoffset: 0;
    }
    40% {
        stroke-dashoffset: 1560;
    }
    80% {
        stroke-dashoffset: 3120;
        fill: transparent;
    }
    100% {
        stroke-dashoffset: 3120;
        fill: #31e8ff;
    }
}

.icon-animation-body .icon-container .icon.star path {
    stroke-dasharray: 1810;
    stroke-dashoffset: 0;
}

.icon-animation-body .icon-container .icon.star:hover path {
    -webkit-animation: animate-star 2s linear forwards;
            animation: animate-star 2s linear forwards;
}

@-webkit-keyframes animate-star {
    0% {
        stroke-dashoffset: 0;
    }
    40% {
        stroke-dashoffset: 1810;
    }
    80% {
        stroke-dashoffset: 3620;
        fill: transparent;
    }
    100% {
        stroke-dashoffset: 3620;
        fill: #31e8ff;
    }
}

@keyframes animate-star {
    0% {
        stroke-dashoffset: 0;
    }
    40% {
        stroke-dashoffset: 1810;
    }
    80% {
        stroke-dashoffset: 3620;
        fill: transparent;
    }
    100% {
        stroke-dashoffset: 3620;
        fill: #31e8ff;
    }
}

.icon-animation-body .icon-container .icon.moon path {
    stroke-dasharray: 1560;
    stroke-dashoffset: 0;
}

.icon-animation-body .icon-container .icon.moon:hover path {
