.scrollbar-demos-body {
    position: relative;
    min-height: 100vh;
    background: black;
    display: flex;
    justify-content: center;
    align-items: center;
}

.scrollbar-progress-body {
    position: relative;
    font-family: "arial";
    width: 80%;
    height: 450px;
    overflow-y: scroll;
}

.scrollbar-progress-body section {
    padding: 50px;
