/* Hover Switch Card Demo */

.hover-switch-card-demo {
    min-height: 100vh;
    font-family: "Poppins", sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(70, 70, 70);
}

.hover-switch-card-demo .container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.hover-switch-card-demo .container .card {
    position: relative;
    width: 300px;
    height: 400px;
    margin: 50px 100px;
}

.hover-switch-card-demo .container .card .face {
    position: absolute;
    width: 100%;
    height: 100%;
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 2px 5px rgba(0, 0, 0, .2);

}

.hover-switch-card-demo .container .card .face.face1 {
    top: 0;
    left: 0;
    transition-property: left, z-index, transform;
    transition-delay: 0s, .3s, .3s;
    transition-duration: .3s, 0s, .3s;
    z-index: 2;
    transform: translate(-10px, -10px);
    border-radius: 15px;
}

.hover-switch-card-demo .container .card:hover .face.face1 {
    transition-property: transform, z-index, left;
    transition-delay: 0s, .3s, .3s;
